.episode-container {
  box-sizing: border-box;
  background-color: #fff;
  border: 2px solid #000;
  grid-template-columns: auto 25%;
  padding: 20px;
  display: grid;
}

.episode-title {
  padding-left: 20px;
  font-weight: bold;
}

.episode-audio {
  float: right;
}

.episode-audio-player {
  background-color: #58c4dd;
  border-radius: 5px;
  width: 300px;
  margin: 20px auto;
  padding: 10px;
  box-shadow: 0 2px 4px #0000001a;
}

audio {
  width: 100%;
  display: block;
}

.episode-audio-player audio::-webkit-media-controls-start-playback-button {
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

audio::-webkit-media-controls-panel {
  background-color: #fff;
}

:is(audio::-webkit-media-controls-current-time-display, audio::-webkit-media-controls-time-remaining-display) {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 14px;
}

@media screen and (width <= 647px) {
  .episode-container {
    box-sizing: border-box;
    background-color: #fff;
    border: 2px solid #000;
    padding: 20px;
    display: flex;
  }

  .episode-title {
    padding-left: 20px;
    font-weight: bold;
  }

  .episode-audio {
    padding-left: 20px;
  }
}

@media screen and (width >= 648px) and (width <= 1138px) {
  episode-container {
    box-sizing: border-box;
    background-color: #fff;
    border: 2px solid #000;
    padding: 20px;
    display: flex;
  }

  .episode-title {
    padding-left: 20px;
    font-weight: bold;
  }

  .episode-audio {
    position: absolute;
    top: 60px;
    right: 0;
  }
}

